<template>
	<view>
		
		<u-navbar :is-back="false" :title="city" :background="background" title-color="#ffffff" title-size="42">
			<view class="navbar-right" slot="right" >
				<view class="right-item">
					<u-image src="@/static/img/qiehuan.png" width="40rpx" height="40rpx" :fade="false" @click="showPickCity = true" ></u-image>
				</view>
			</view>
			
		</u-navbar>
		
		<!-- 定义样式 -->
		<view class="content">
		<!-- <view>
			<u-link href="http://www.uviewui.com"></u-link>
		</view> -->
			
			<!-- 城市选择层 -->
			<u-picker v-model="showPickCity" mode="region" :params="cityParams" @confirm="confirm"></u-picker>
			
			<view class="space"></view>
			<view class="space"></view>
			<view class="space"></view>
			<view class="space"></view>
			<view class="space"></view>
			<view class="space"></view>
			<view class="space"></view>
			<view class="space"></view>
			
			<view class="row-padding">
				<!-- 温度 -->
				<view>
					<!--提示字 -->
					<view class="content">
						<text class="text">温度 | {{temDesc}}</text>
					</view>
					<view class="space"></view>
					<!--温度值 -->
					<view >
						<text class="Val">{{temperatureVal}}</text>  <text class="white-font">&#176;C</text>
					</view>
					
				</view>
					
					<!-- 湿度 -->
				<view class="content left-padding">
					<!--提示字 -->
					<view>
						<text class="text">湿度 | {{RHDesc}}</text>
					</view>
					<view class="space"></view>
					<!--湿度值 -->
					<view class="u-row">
						<text class="Val">{{RHVal}}</text>  <text class="white-font">%</text>
					</view>
				</view>
			
			</view>
			
			
			<!-- <view src="src">繁华的寂静</view> -->
			
			<!-- 图表 -->
			<view class="qiun-columns">
				<view class="qiun-bg-white qiun-title-bar qiun-common-mt" >
					<view class="qiun-title-dot-light">温度变化图</view>
					<!-- <view class="space"></view> -->
					</view>
				<view class="qiun-charts" >
					<canvas canvas-id="canvasLineA" id="canvasLineA" class="charts" @touchstart="touchLineA"></canvas>
				</view>
				
				<!-- <view class="space"></view>
				<u-line color="red" length="80%" border-style="dashed" margin="center"></u-line> -->
				
				<view class="qiun-bg-white qiun-title-bar qiun-common-mt" >
					<view class="qiun-title-dot-light">湿度变化图</view>
				</view>
				<view class="qiun-charts" >
					<canvas canvas-id="canvasLineB" id="canvasLineB" class="charts" @touchstart="touchLineB"></canvas>
				</view>
				
				
				<!-- <view class="qiun-bg-white qiun-title-bar qiun-common-mt" >
					<view class="qiun-title-dot-light white-font">昆明温度变化图</view>
				</view>
				<view class="qiun-charts" >
					<canvas canvas-id="canvasLineC" id="canvasLineC" class="charts" @touchstart="touchLineC"></canvas>
				</view> -->
				
				<view class="qiun-bg-white qiun-title-bar qiun-common-mt" >
							<view class="qiun-title-dot-light">温度计式图表</view>
						</view>
						<view class="qiun-charts" >
							<canvas canvas-id="canvasColumnMeter" id="canvasColumnMeter" class="charts"  @touchstart="touchColumn"></canvas>
						</view>

				
				
			</view>
			
		</view>
	</view>
	
	
	
</template>

<script>
	// 引用，把uCharts导入进来
	import uCharts from '../../components/u-charts/u-charts.js';
	// 导入json文件
	import data from "@/static/data.json"
	// 后边赋值了，=this
	var _self;
	var canvaLineA=null;
	var canvaLineB=null;
	// var canvaLineC=null; //昆明温度图
    var canvaColumn=null;
  	
	// var music=null;
	
	
	export default {
		
		// components: { uniPagination },
		
		data() {
			return {
				//顶栏背景
				background:{
					backgroundColor:"#55aaff"
				},
				
				//是否显示城市选择层
				showPickCity:false,
				
				//令城市只选择省和市
				cityParams:{
					province: true,
					city: true,
					area: false,
				},
				
				// 温度值
				temperatureVal:"",
				// 湿度值
				RHVal:"",
				
				// 温度描述
				temDesc:"",
				// 湿度描述
				RHDesc:"",
				
				// 定义宽度
				cWidth:'',
				// 定义高度
				cHeight:'',
				// 分辨率
				pixelRatio:1,
				KMTemData:'',

				city:"北京市",
				
				// 定义温度时间数组，温度数组
				temTimeList:[],
				temList:[],
				
				// 定义湿度时间数组，湿度数组
				RHTimeList:[],
				RHList:[],
				
				//一个放昆明温度的横坐标（日期），另一个放纵坐标（温度
				KMTimeList:[],
				KMTemList:[]
				// KMTyList:[]
				
			}
		},
		
		// 页面加载之后执行
		onLoad() { 

			// 为_this赋值，定义图表的宽度和高度
			_self = this;
			this.cWidth=uni.upx2px(750);
			this.cHeight=uni.upx2px(500);
			
			
			//画图
			this.getTemData();
			this.getRHData();
			
			this.getKMTemData();
			
			
			//获取最后时刻的温湿度
			this.temperatureVal=this.temList[this.temList.length-1]
			this.RHVal=this.RHList[this.RHList.length-1]
			
			//判断当前温湿度的描述
			this.determineVal()
			
			//获取网络json数据
			this.getWebJson();
				
			},
		
		methods: {
			
			//选择地区之后的回调函数，将选择的城市剥离出来
			confirm(e) {
				if(e.province.label=="北京市"||e.province.label=="天津市"||e.province.label=="重庆市"||e.province.label=="上海市"){
					this.city = e.province.label;
				}
				else{
					this.city = e.city.label;
				}
				
			},
			
			getWebJson(){
				console.log(this.city);
				uni.request({
				    url: "https://api.jisuapi.com/weather/query",//这个是你要请求的地址
				    data: {
						appkey:"2240709f50c9dad4",
						city:"北京"
						//这个data是指，你看你从网络地址请求数据，你总得打个招呼吧，所以要给它一些信息，但是如果对方不要，我们就不给，这个情况我们也不需要给
				    },
					timeout:120000,
				    header: {
						//请求头信息，可不填（如果 没特殊要求就不填）
				    },
				    success: (res) => {//这句话就是，如果请求成功了，那么返回res，下面是请求成功之后的操作
						console.log(res)
						// var weatherList;
						// weatherList = res.data.data.forecast;
						// //console.log(weatherList);
						// //遍历weatherList数组
						// for(let item of weatherList){
						// 	//把日期塞进去
						// 	this.KMTimeList.push(parseInt(item.date));							
						// 	//塞完日期塞温度							
						// 	this.KMTemList.push( (parseInt( item.high.replace(/[^0-9]/ig,"") ) + parseInt( item.low.replace(/[^0-9]/ig,"") ))/2 );
						// 	// this.KMTyList.push(parseInt(item.type));
							
						// }
						
				    },
					fail: (res) => {
						console.log(res)
					}
				});
				
				
			},
			
			
			// 判断
			determineVal(){
				//温度，字符串不能和数值直接比较大小，故用parseFloat将字符串转换为浮点型数据输出
				if(parseFloat(this.temperatureVal)>25.0)
				{
					this.temDesc="偏热";
				}
				else if(parseFloat(this.temperatureVal)<=25.0 && parseFloat(this.temperatureVal)>15.0)
				{
					this.temDesc="适宜";
				}
				else if(parseFloat(this.temperatureVal)<=15.0){
					this.temDesc="偏冷";
				}
				
				//湿度
				if(parseFloat(this.RHVal)>70.0)
				{
					this.RHDesc="潮湿";
				}
				else if(parseFloat(this.RHVal)<=70.0 && parseFloat(this.RHVal)>40.0)
				{
					this.RHDesc="适宜";
				}
				else if(parseFloat(this.RHVal)<=40.0){
					this.RHDesc="干燥";
				}				
			},
			
			
			getTemData(){
				let LineA ={
					categories:this.temTimeList,
					series:[{
						data:this.temList,
						name:"温度"
					}]
				}
				_self.showLineA("canvasLineA",LineA);
				
				
				
				// uni.request({
				// 	url: 'https://www.ucharts.cn/data.json',
				// 	data:{
				// 	},
				// 	success: function(res) {
				// 		console.log(res.data.data)
				// 		let LineA={categories:[],series:[]};
				// 		//这里我后台返回的是数组，所以用等于，如果您后台返回的是单条数据，需要push进去
				// 		LineA.categories=res.data.data.LineA.categories;
				// 		LineA.series=res.data.data.LineA.series;
				// 		_self.showLineA("canvasLineA",LineA);
				// 	},
				// 	fail: () => {
				// 		_self.tips="网络错误，小程序端请检查合法域名";
				// 	},
				// });
			},
			
			getRHData(){
				
			//这里后台返回的是数组，所以用等于，如果后台返回的是单条数据，需要push进去

				let LineB ={
					categories:this.RHTimeList,
					series:[{
						data:this.RHList,
						name:"湿度"
					}]
				}
				_self.showLineB("canvasLineB",LineB);
			},
			
			
			
			
			//昆明温度变化图C
			getKMTemData(){
				let ColumnMeter ={
					categories:this.KMTimeList,
					series:[{
						data:this.KMTemList,
						// data:this.KMTyList,
						name:"昆明日均气温"
					}]
				}
				_self.showColumnMeter("canvasColumnMeter",ColumnMeter);
				
			
			},
			
			
			
			
			
			// 图标配置
			showLineA(canvasId,chartData){
				canvaLineA=new uCharts({
					$this:_self,
					canvasId: canvasId,
					type: 'line',
					fontSize:11,
					legend:{show:true},
					dataLabel:false,
					dataPointShape:true,
					background:'#FFFFFF',
					pixelRatio:_self.pixelRatio,
					categories: chartData.categories,
					series: chartData.series,
					animation: true,
					xAxis: {
						type:'grid',
						gridColor:'#CCCCCC',
						gridType:'dash',
						dashLength:8
					},
					yAxis: {
						gridType:'dash',
						gridColor:'#CCCCCC',
						dashLength:8,
						splitNumber:5,
						min:0,
						max:40,
						format:(val)=>{return val.toFixed(0)+'℃'
					}
					},
					width: _self.cWidth*_self.pixelRatio,
					height: _self.cHeight*_self.pixelRatio,
					extra: {
						line:{
							type: 'straight'
						}
					}
				});				
			},
			
			showLineB(canvasId,chartData){
				canvaLineB=new uCharts({
					$this:_self,
					canvasId: canvasId,
					type: 'line',
					fontSize:11,
					legend:{show:true},
					dataLabel:false,
					dataPointShape:true,
					background:'#FFFFFF',
					pixelRatio:_self.pixelRatio,
					categories: chartData.categories,
					series: chartData.series,
					animation: true,
					xAxis: {
						type:'grid',
						gridColor:'#CCCCCC',
						gridType:'dash',
						dashLength:8
					},
					yAxis: {
						gridType:'dash',
						gridColor:'#CCCCCC',
						dashLength:8,
						splitNumber:5,
						min:0,
						max:100,
						format:(val)=>{return val.toFixed(0)+'%'
					}

					},
					width: _self.cWidth*_self.pixelRatio,
					height: _self.cHeight*_self.pixelRatio,
					extra: {
						line:{
							type: 'straight'
						}
					}
				});				
			},
			

			
			
			showColumnMeter(canvasId,chartData){
				canvaColumn=new uCharts({
					$this:_self,
					canvasId: canvasId,
					type: 'column',
					legend:{show:true},
					fontSize:11,
					background:'#FFFFFF',
					pixelRatio:_self.pixelRatio,
					animation: true,
					categories: chartData.categories,
					series: chartData.series,
					xAxis: {
						disableGrid:true,
					},
					yAxis: {
					},
					dataLabel: true,
					width: _self.cWidth*_self.pixelRatio,
					height: _self.cHeight*_self.pixelRatio,
					extra: {
						column: {
							type:'meter',
							width: _self.cWidth*_self.pixelRatio*0.45/chartData.categories.length,
							meter:{
								//这个是外层边框的宽度
								border:4,
								//这个是内部填充颜色
								fillColor:'#550000'
							}
						}
					  },
				});
				
			},
			
			// 触碰折线图上的点获得数据
			touchLineA(e) {
				canvaLineA.showToolTip(e, {
					format: function (item, category) {
						return category + ' ' + item.name + ':' + item.data 
					}
				});
			},
			
			touchLineB(e) {
				canvaLineB.showToolTip(e, {
					format: function (item, category) {
						return category + ' ' + item.name + ':' + item.data 
					}
				});
			},
			

	
			touchColumn(e){
				canvaColumn.showToolTip(e, {
					format: function (item, category) 
					{
						if(typeof item.data === 'object')
						{
							return category + ' ' + item.name + ':' + item.data.value 
						}
						else
						{
							return category + ' ' + item.name + ':' + item.data 
						}
						
					},
				});
			}
		
			}
		}

	
</script>


<style>
	page{
		background-color: #55aaff;
	}
		
	.navbar-right {
		margin-right: 24rpx;
		display: flex;
	}
	
	.right-item {
		margin: 0 12rpx;
		position: relative;
		color: #ffffff;
		display: flex;
	}
		
	.qiun-charts {
		width: 750upx;
		height: 500upx;
		background-color: #FFFFFF;
	}
	
	.charts {
		width: 750upx;
		height: 500upx;
		background-color: #FFFFFF;
	}

	.white-font{
		color: #FFFFFF;
	}
	
	.Val{
		font-weight: 600;
		color:#FFFFFF;
		font-family:Arial, Helvetica, sans-serif;
		font-size: 100rpx;
	}
	
	.text{
		color: #ffffff;
	}
	
	.space{
		height: 20rpx;
	}
	
	.content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}
	
	.row-padding{
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;
	}
	
	.left-padding{
		padding-left: 90rpx;
	}
	
	.column-padding{
		margin-top: 0rpx;
		display:flex;
		flex-direction:column;
		
	}

	.logo {
		height: 200rpx;
		width: 200rpx;
		margin-top: 200rpx;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 50rpx;
	}

	.text-area {
		display: flex;
		justify-content: center;
	}

	.title {
		font-size: 50rpx;
		color: #8f8f94;
	}
	
	.qiun-padding{
	    padding:2%; 
		width:96%;
		}
		
	
	.qiun-wrap{
		display:flex;
		 flex-wrap:wrap;
		 }
		 
	.qiun-common-mt{
		margin-top:10upx;
		
		}

	
</style>
